.link{
  text-decoration: none;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 10px 15px;
  justify-content: center;
  color: white;
}
.link div {
  background-color: #fff;
}
.active{
  color: yellow;
}
.active div{
  background-color: yellow;
}


@keyframes heal {
  0% {
    background-color: darkred;
    transform: translateY(20px) ;
  }
  100% {
    background-color: darkred;
    transform: translateY(0px) ;
  }
}
.heal{
  width: 20px;
  height: 20px;
  animation-delay: .04s;
  animation-duration: 10.09s;
  animation-name: heal;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
}
.text{
  align-items: center;
  display: flex;

  flex-direction: row;
  font-size: 1rem;  
}
